"use client"

import { cn } from "@/lib/utils"

import type React from "react"

import { useState, useEffect } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { format, parse } from "date-fns"
import { type CalendarEvent, useCalendar } from "@/context/calendar-context"

interface AddEventModalProps {
  isOpen: boolean
  onClose: () => void
  selectedEvent?: CalendarEvent
}

export function AddEventModal({ isOpen, onClose, selectedEvent }: AddEventModalProps) {
  const { addEvent, updateEvent } = useCalendar()

  const [formData, setFormData] = useState({
    title: "",
    startDate: format(new Date(), "yyyy-MM-dd"),
    startTime: "09:00",
    endDate: format(new Date(), "yyyy-MM-dd"),
    endTime: "10:00",
    address: "",
    description: "",
    color: "bg-blue-100 text-blue-800",
  })

  // Reset form when modal opens/closes or selected event changes
  useEffect(() => {
    if (selectedEvent) {
      setFormData({
        title: selectedEvent.title,
        startDate: format(selectedEvent.start, "yyyy-MM-dd"),
        startTime: format(selectedEvent.start, "HH:mm"),
        endDate: format(selectedEvent.end, "yyyy-MM-dd"),
        endTime: format(selectedEvent.end, "HH:mm"),
        address: selectedEvent.address || "",
        description: selectedEvent.description || "",
        color: selectedEvent.color || "bg-blue-100 text-blue-800",
      })
    } else {
      setFormData({
        title: "",
        startDate: format(new Date(), "yyyy-MM-dd"),
        startTime: "09:00",
        endDate: format(new Date(), "yyyy-MM-dd"),
        endTime: "10:00",
        address: "",
        description: "",
        color: "bg-blue-100 text-blue-800",
      })
    }
  }, [isOpen, selectedEvent])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleColorChange = (color: string) => {
    setFormData((prev) => ({ ...prev, color }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()

    const startDateTime = parse(`${formData.startDate} ${formData.startTime}`, "yyyy-MM-dd HH:mm", new Date())

    const endDateTime = parse(`${formData.endDate} ${formData.endTime}`, "yyyy-MM-dd HH:mm", new Date())

    const eventData = {
      title: formData.title,
      start: startDateTime,
      end: endDateTime,
      address: formData.address,
      description: formData.description,
      color: formData.color,
    }

    if (selectedEvent) {
      updateEvent(selectedEvent.id, eventData)
    } else {
      addEvent(eventData)
    }

    onClose()
  }

  const colorOptions = [
    { value: "bg-blue-100 text-blue-800", label: "Blue" },
    { value: "bg-purple-100 text-purple-800", label: "Purple" },
    { value: "bg-orange-100 text-orange-800", label: "Orange" },
    { value: "bg-green-100 text-green-800", label: "Green" },
    { value: "bg-red-100 text-red-800", label: "Red" },
    { value: "bg-gray-200 text-gray-800", label: "Gray" },
  ]

  return (
    <Dialog open={isOpen} onOpenChange={(open) => !open && onClose()}>
      <DialogContent className="sm:max-w-[500px]">
        <DialogHeader>
          <DialogTitle>{selectedEvent ? "Edit Booking" : "Add Booking"}</DialogTitle>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="title">Booking Name</Label>
            <Input
              id="title"
              name="title"
              value={formData.title}
              onChange={handleChange}
              placeholder="Enter booking name"
              required
            />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="startDate">Start Date</Label>
              <Input
                id="startDate"
                name="startDate"
                type="date"
                value={formData.startDate}
                onChange={handleChange}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="startTime">Start Time</Label>
              <Input
                id="startTime"
                name="startTime"
                type="time"
                value={formData.startTime}
                onChange={handleChange}
                required
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="endDate">End Date</Label>
              <Input
                id="endDate"
                name="endDate"
                type="date"
                value={formData.endDate}
                onChange={handleChange}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="endTime">End Time</Label>
              <Input
                id="endTime"
                name="endTime"
                type="time"
                value={formData.endTime}
                onChange={handleChange}
                required
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label>Color</Label>
            <div className="flex flex-wrap gap-2">
              {colorOptions.map((color) => (
                <div
                  key={color.value}
                  className={cn(
                    "h-8 w-8 cursor-pointer rounded-full border-2",
                    color.value,
                    formData.color === color.value ? "border-gray-900" : "border-transparent",
                  )}
                  onClick={() => handleColorChange(color.value)}
                  title={color.label}
                />
              ))}
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="address">Address/City</Label>
            <Textarea
              id="address"
              name="address"
              value={formData.address}
              onChange={handleChange}
              placeholder="Enter address or city"
              rows={2}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="description">Description</Label>
            <Textarea
              id="description"
              name="description"
              value={formData.description}
              onChange={handleChange}
              placeholder="Enter description"
              rows={3}
            />
          </div>

          <div className="flex justify-end space-x-2">
            <Button type="button" variant="outline" onClick={onClose}>
              Cancel
            </Button>
            <Button type="submit" className="bg-blue-600 text-white hover:bg-blue-700">
              {selectedEvent ? "Update" : "Add"} Booking
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
