"use client"

import { useCalendar } from "@/context/calendar-context"
import { cn } from "@/lib/utils"
import { format, addDays, isSameDay, startOfWeek } from "date-fns"

interface WeeklyCalendarProps {
  onEventClick: (eventId: string) => void
}

export function WeeklyCalendar({ onEventClick }: WeeklyCalendarProps) {
  const { events } = useCalendar()

  // Set the start date to May 5th, 2025 (Monday of the current week)
  const today = new Date(2025, 4, 9) // May 9th, 2025
  const startDate = startOfWeek(today, { weekStartsOn: 1 }) // Start from Monday

  // Generate the week days
  const weekDays = Array.from({ length: 7 }, (_, i) => {
    const date = addDays(startDate, i)
    return {
      date,
      dayName: format(date, "EEEE"),
      dayNumber: format(date, "d"),
      isToday: isSameDay(date, today),
    }
  })

  // Generate time slots for 24 hours
  const timeSlots = Array.from({ length: 24 }, (_, i) => {
    const hour = i
    const amPm = hour < 12 ? "AM" : "PM"
    const hour12 = hour === 0 ? 12 : hour > 12 ? hour - 12 : hour
    return {
      hour,
      label: `${hour12.toString().padStart(2, "0")}:00 ${amPm}`,
    }
  })

  // Add half-hour slot for 12:30
  const allTimeSlots = [
    ...timeSlots.slice(0, 12), // 12AM to 12PM
    { hour: 12.5, label: "12:30 PM" },
    ...timeSlots.slice(13), // 1PM to 11PM
  ]

  // Filter and organize events by day and time
  const getEventsForDayAndTime = (date: Date, hour: number) => {
    return events.filter((event) => {
      const eventStartHour = event.start.getHours() + event.start.getMinutes() / 60
      const eventEndHour = event.end.getHours() + event.end.getMinutes() / 60

      return isSameDay(event.start, date) && eventStartHour <= hour && eventEndHour > hour
    })
  }

  return (
    <div className="relative flex flex-col">
      {/* Header with days */}
      <div className="sticky top-0 z-50 grid grid-cols-[80px_1fr_1fr_1fr_1fr_1fr_1fr_1fr] bg-white">
        <div className="border-b  p-2 text-center text-xs font-medium text-gray-500">GMT +07</div>
        {weekDays.map((day) => (
          <div key={day.dayName} className={cn("border-b  p-2 text-center", day.isToday ? "bg-blue-50" : "")}>
            <div className="text-sm font-medium">
              {day.dayName}, {day.dayNumber}
            </div>
          </div>
        ))}
      </div>

      {/* Time slots and events */}
      <div className="min-w-[800px] overflow-x-auto">
        {allTimeSlots.map((timeSlot) => (
          <div key={timeSlot.label} className="grid grid-cols-[80px_1fr_1fr_1fr_1fr_1fr_1fr_1fr]">
            {/* Time label */}
            <div className="border-r p-2 text-right text-xs font-medium text-gray-500">{timeSlot.label}</div>

            {/* Day columns */}
            {weekDays.map((day) => {
              const dayEvents = getEventsForDayAndTime(day.date, timeSlot.hour)

              return (
                <div
                  key={`${day.dayName}-${timeSlot.label}`}
                  className={cn("relative min-h-[50px] border-r", day.isToday ? "bg-blue-50" : "")}
                >
                  {dayEvents.map((event) => {
                    const isStartHour = event.start.getHours() + event.start.getMinutes() / 60 === timeSlot.hour

                    if (!isStartHour) return null

                    const startHour = event.start.getHours() + event.start.getMinutes() / 60
                    const endHour = event.end.getHours() + event.end.getMinutes() / 60
                    const durationHours = endHour - startHour
                    const heightInPixels = durationHours * 50 // Each hour is 50px tall

                    const startTimeStr = format(event.start, "h:mm a")
                    const endTimeStr = format(event.end, "h:mm a")

                    return (
                      <div
                        key={event.id}
                        className={cn(
                          "absolute left-0 right-0 p-1 z-10 m-1 cursor-pointer overflow-hidden rounded text-xs",
                          event.color || "bg-blue-100 text-blue-800",
                        )}
                        style={{ height: `${heightInPixels - 2}px` }}
                        onClick={() => onEventClick(event.id)}
                      >
                        <div className={`font-medium p-2 bg-${event.color} opacity-100 border`}>{event.title}</div>
                        <div className="text-xs px-2 opacity-80">
                          {startTimeStr}-{endTimeStr}
                        </div>
                        {event.description && <div className="mt-1 px-2 text-xs">{event.description}</div>}
                      </div>
                    )
                  })}
                </div>
              )
            })}
          </div>
        ))}
      </div>
    </div>
  )
}
