"use client"

import { useState } from "react"
import { Bell, ChevronDown, Grid, List, Search } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { AddEventModal } from "./add-event-modal"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { WeeklyCalendar } from "./weekly-calendar"
import { useCalendar } from "@/context/calendar-context"

export function CalendarView() {
  const [isAddEventModalOpen, setIsAddEventModalOpen] = useState(false)
  const [selectedEventId, setSelectedEventId] = useState<string | null>(null)
  const { getEventById } = useCalendar()

  const handleEventClick = (eventId: string) => {
    setSelectedEventId(eventId)
    setIsAddEventModalOpen(true)
  }

  const selectedEvent = selectedEventId ? getEventById(selectedEventId) : undefined

  return (
    <div className="flex h-full flex-col">
      {/* Header */}
      <header className="flex items-center justify-between border-b p-3">
        <div className="flex items-center gap-2">
          <div className="relative w-64">
            <Search className="absolute left-2 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
            <Input placeholder="Search" className="h-9 pl-8 rounded-4xl" />
          </div>
        </div>
        <div className="flex items-center gap-3">
          <Button variant="ghost" size="icon" className="h-9 w-9 rounded-full">
            <Bell className="h-5 w-5" />
          </Button>
          <Select defaultValue="las-sabir">
            <SelectTrigger className="w-[180px] border-0 font-normal">
              <SelectValue placeholder="Select user" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="las-sabir">Las Sabir</SelectItem>
              <SelectItem value="john-doe">John Doe</SelectItem>
              <SelectItem value="jane-smith">Jane Smith</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </header>

      {/* Calendar Controls */}
      <div className="flex items-center justify-between border-b p-3">
        <div className="flex items-center gap-3">
          <div className="flex items-center border bg-gray-100 rounded-lg p-1">
            <Button variant="outline" size="icon" className="h-8 w-8 border-0 rounded">
              <Grid className="h-4 w-4" />
            </Button>
            <Button variant="ghost" size="icon" className="h-8 w-8">
              <List className="h-4 w-4" />
            </Button>
          </div>

          <div className="flex items-center gap-2">
            <Button variant="outline" className="flex items-center gap-1">
              Week
              <ChevronDown className="h-4 w-4" />
            </Button>
          </div>

          <Button variant="outline">Today</Button>
        </div>

        <div className="flex items-center gap-2">
          <Button variant="outline" className="flex items-center gap-1">
            Filter
            <ChevronDown className="h-4 w-4" />
          </Button>
          <Button variant="outline">View Waitlist</Button>
          <Button
            className="bg-blue-600 text-white hover:bg-blue-700"
            onClick={() => {
              setSelectedEventId(null)
              setIsAddEventModalOpen(true)
            }}
          >
            Add Booking
          </Button>
        </div>
      </div>

      {/* Status Tabs */}
      <div className="flex justify-between border-b px-3 py-2">
        <div className="flex">
          <div className="flex items-center gap-1 px-4 py-1 hover:bg-gray-50">
            <span className="text-gray-600">Upcoming</span>
            <span className="font-medium">5</span>
          </div>
          <div className="flex items-center gap-1 px-4 py-1 hover:bg-gray-50">
            <span className="text-gray-600">Completed</span>
            <span className="font-medium">8</span>
          </div>
          <div className="flex items-center gap-1 px-4 py-1 hover:bg-gray-50">
            <span className="text-gray-600">Cancelled</span>
            <span className="font-medium">0</span>
          </div>
        </div>
      </div>

      {/* Calendar */}
      <div className="flex-1 overflow-auto">
        <WeeklyCalendar onEventClick={handleEventClick} />
      </div>

      {/* Add/Edit Event Modal */}
      <AddEventModal
        isOpen={isAddEventModalOpen}
        onClose={() => setIsAddEventModalOpen(false)}
        selectedEvent={selectedEvent}
      />
    </div>
  )
}
